import {Component, OnDestroy, OnInit} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {ResumenBajasService} from '../services/resumen-bajas.service';
import {Subscription} from 'rxjs/Subscription';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {TipoDocumento} from '../models/tipo_documento';
import {Series} from '../models/series';
import {Comprobante} from '../models/comprobante';
import {Anulacion} from '../models/anulacion';
import {Bajasfiltro} from '../models/bajas_filtro';
declare interface DataTable {
  headerRow: string[];
  footerRow: string[];
  dataRows: string[][];
}

declare var $: any;
declare var swal: any;

let datatable;

declare var DatatableFunctions: any;

@Component({
  selector: 'app-resumen-bajas-crear',
  templateUrl: './resumen-bajas-crear.component.html',
  styleUrls: ['./resumen-bajas-crear.css']
})
export class ResumenBajasCrearComponent implements OnInit, OnDestroy {

  public productFormGroup: FormGroup;
  private filtroBajas: Bajasfiltro;
  public tipoDocumento: TipoDocumento[];
  public serie: Series[];
  private comprobante: Comprobante[];
  private anulacion: Anulacion[];
  public esguia: number;
  saveButtonName = 'Guardar';
  subscriptionTipoDocumentos: Subscription;
  subscriptionComprobantes: Subscription;
  subscriptionAnulacion: Subscription;
  subscriptionSeries: Subscription;
  editMode = false;
  id: number;

  showSwal() {
         swal({
        title: '¿Está Seguro?',
        html:
             '<div class="text-center"> Desea dar de baja a el/los comprobante(s) </div>',
        padding: '20',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: 'SI',
        cancelButtonText: 'NO',
        confirmButtonClass: 'btn btn-success',
        cancelButtonClass: 'btn btn-danger',
        buttonsStyling: false
      }).then(function() {
        swal({
          title: 'Acción Existosa',
          html:
            '<div class="text-center"> El/los comprobante(s) iniciarion proceso de baja. </div>',
          type: 'success',
          confirmButtonClass: 'btn btn-success',
          buttonsStyling: false
        });
      });
    }

  constructor(private anulacionService: ResumenBajasService,
              private route: ActivatedRoute,
              private router: Router) {this.filtroBajas = new Bajasfiltro();
  }

  ngOnInit() {
    this.esguia = 0;
    cargarDataTable(this.data);
    this.route.params
      .subscribe(
        (params: Params) => {
          this.id = +params['id'];
          this.editMode = params['id'] != null;
          if (this.editMode)
            this.saveButtonName = 'Editar';
          this.subscriptionTipoDocumentos = this.anulacionService.tipoDocumentoChanged
            .subscribe(
              (tipos: TipoDocumento[]) => {
                this.tipoDocumento = tipos;
              }
            );
          this.subscriptionSeries = this.anulacionService.serieChanged
            .subscribe(
              (tipos: Series[]) => {
                this.serie = tipos;
              }
            );
          this.initForm();
        }
      );
  }
  public data = [
    {
      'id': 1,
      'serie': 'S001',
      'correlativo': '10000001',
      'fecha': '21-10-2017',
      'total': '10.20',
    },
    {
      'id': 2,
      'serie': 'S002',
      'correlativo': '10000002',
      'fecha': '23-10-2017',
      'total': '2323.45',
    }
  ];
  public data1 = [
    {
      'id': 1,
      'serie': 'S001',
      'correlativo': '10000001',
      'fecha': '21-10-2017',
    },
    {
      'id': 2,
      'serie': 'S002',
      'correlativo': '10000002',
      'fecha': '23-10-2017',
    }
  ];
  ngOnDestroy() {
    this.subscriptionTipoDocumentos.unsubscribe();
    this.subscriptionSeries.unsubscribe();
  }
  private initForm() {
    this.productFormGroup = new FormGroup({
      'cmbtipodoc': new FormControl(),
      'cmbserie': new FormControl(),
      'correlativoinicio': new FormControl(),
      'correlativofinal': new FormControl(),
      'fechaemisioninicio':  new FormControl(),
      'fechaemisionfin': new FormControl(),
      'motivo': new FormControl(),
    });
  }

  cambiar_datable() {
    if (this.productFormGroup.get('cmbtipodoc').value == 7 ||this.productFormGroup.get('cmbtipodoc').value == 6  ) {
      cargarDataTableGuia(this.data1);
      this.esguia = 1;

    }
    else {
      this.esguia = 0;
      cargarDataTable(this.data);
    }
  }

  limpiar() {
    this.productFormGroup.reset();
    setTimeout(function () {
      $('input').each(function () {
        $(this.parentElement).addClass('is-empty');
      });
      $('select').each(function () {
        $(this.parentElement).addClass('is-empty');
      });
    }, 200);
  }
  filtro($event) {
    this.fillProducto();
  }
  onSubmit() {
    if (this.editMode) {
      this.fillProducto();
     // this.anulacionService.update(this.producto);
    }
    else {
      this.fillProducto();
     // this.anulacionService.add(this.producto);
      this.productFormGroup.reset();
    }
    this.onCancel();
  }
  fillProducto() {
    this.filtroBajas.tipodocumento = this.tipoDocumento[this.tipoDocumento.findIndex
    (element => element.id === this.productFormGroup.get('cmbtipodoc').value)];
    this.filtroBajas.serie = this.serie[this.serie.findIndex(element => element.id === this.productFormGroup.get('cmbserie').value)];
    this.filtroBajas.correlativoini =  this.productFormGroup.get('correlativoinicio').value;
    this.filtroBajas.correlativofin =  this.productFormGroup.get('correlativofinal').value;
    this.filtroBajas.fechaini = this.productFormGroup.get('fechaemisioninicio').value;
    this.filtroBajas.fechafin = this.productFormGroup.get('fechaemisionfin').value;
  }
  onCancel() {
    if (this.editMode)
      this.router.navigate(['../../consultar'], {relativeTo: this.route, skipLocationChange: true});
    else
      this.router.navigate(['../consultar'], {relativeTo: this.route});
  }

}
function cargarDataTable(data: Array<any>) {

    datatable = $('#dtresumencomprobantes').DataTable({
      searching: false,
      columns: [
        { data: '0', name: '0' },
        { data: 'id', name: 'id' },
        { data: 'serie', name: 'serie' },
        { data: 'correlativo', name: 'correlativo' },
        { data: 'fecha', name: 'fecha' },
        { data: 'total', name: 'total' },
      ],
      columnDefs: [
        { 'className': 'text-center', 'targets': [1, 2, 3, 4] },
        {
          render: function ( type, row) {
            return '<div class="text-right" height="100%">' +
              '<div class="checkbox text-right">' +
              '<label><input type="checkbox" name="optionsCheckboxes"></label>' +
              '</div>' +
              '</div>';
          },
          targets: 0,
          orderable: false
        },
      ],
      data: data
    });
}
function cargarDataTableGuia(data: Array<any>) {

  datatable = $('#dtresumencomprobantesguia').DataTable({
    searching: false,
    columns: [
      { data: '0', name: '0' },
      { data: 'id', name: 'id' },
      { data: 'serie', name: 'serie' },
      { data: 'correlativo', name: 'correlativo' },
      { data: 'fecha', name: 'fecha' },
    ],
    columnDefs: [
      { 'className': 'text-center', 'targets': [1, 2, 3] },
      {
        render: function ( type, row) {
          return '<div class="text-right" height="100%">' +
            '<div class="checkbox text-right">' +
            '<label><input type="checkbox" name="optionsCheckboxes"></label>' +
            '</div>' +
            '</div>';
        },
        targets: 0,
        orderable: false
      },
    ],
    data: data
  });
}



